.column {
  flex-direction: column;
}

.custom-column {
  color: #111111;
  background-color: #fff;
  border: 1px solid #c4c4c4;
  text-transform: capitalize;
}

.custom-column:active,
.custom-column:hover {
  color: @brand-color;
  font-weight: 700;
  border: 1px solid @brand-color;
}

.upper {
  text-transform: uppercase;
}

.no-text {
  padding: 0 !important;
  background-color: #f8f8f8;
}

.main-blocks {
  padding-top: 100px;

  max-width: 1039px;
  margin: 0 auto;
}

.main-block {
  position: relative;
  display: flex;
}

.main-block::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 30px;
  width: 1px;
  background-color: #c4c4c4;
  z-index: 1;
}

.main-block:nth-child(1)::before {
  top: 20px;
}

.main-block:nth-child(9)::before {
  bottom: -20px;
}

.main-block:nth-child(10)::before,
.main-block:nth-child(11)::before {
  display: none;
}

.main-block-step {
  margin: 10px 0;
  margin-left: 90px;
  padding: 24px 30px;
  width: 100%;
  background-color: #fff;
  border: 1px solid #c4c4c4;
  border-radius: 3px;
}

.index-num {
  position: absolute;
  display: flex;
  justify-content: center;
  margin: 10px 31px 10px 0;
  padding: 17px 24px;
  background-color: #fff;
  border: 1px solid #c4c4c4;
  max-width: 58px;
  max-height: 58px;
  text-align: center;
  z-index: 2;
}

.main-block-step-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.main-block-step-body {
  display: none;
}

.main-block.active {
  .index-num {
    border-color: @brand-color;
    background-color: @brand-color;
    color: #fff;
  }
  .main-block-step {
    background-color: #f8f8f8;
  }
}

.main-block.passed {
  .main-block-step-check {
    background-image: url("../img/check-active.svg");
  }
}

.main-block.active .custom-bg {
  background-image: url("../img/step-bg.png");
  background-repeat: no-repeat;
  background-position: bottom;
  background-size: contain;
}

.main-block:nth-child(11) .main-block-step-body {
  width: 100%;
}

.main-block-step-title {
  color: #686868;
  font-size: 18px;
  line-height: 24px;
}

.main-block-step-check {
  display: block;
  padding: 15px;
  background-image: url("../img/check.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 100%;
  max-width: 24px;
  height: 24px;
}

.main-block-step-choose {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
}

.main-block-step-num {
  font-size: 14px;
  line-height: 22;
}

.main-block-step-bottom {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.main-step-block {
  max-width: 430px;
}

.main-step-block img {
  max-width: 205px;
}

.main-block-step-bottom-cta {
  display: flex;
  flex-direction: column;
  margin: 10px;
  max-width: 396px;
}

.main-block-step-bottom-answer {
  padding: 18px;
  text-align: left;
  cursor: pointer;
  border-radius: 3px;
}

.main-block-step-bottom-img:hover {
  transition: 0.5s ease;
  box-shadow: 0px 0px 8px 3px @brand-color;
}

.main-block-form {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin-left: 0;
  background-color: @brand-color !important;
}

.main-block-form-text {
  display: block;
  padding-right: 10px;
  width: 100%;
  max-width: 358px;
}

.main-block-form-title {
  color: #fff;
  font-size: 24px;
  line-height: 36px;
}

.main-block-form-descr {
  color: #fff;
  margin-bottom: 58px;
}

.main-block-form-cta {
  color: #fff;
  font-weight: 700;
  font-size: 24px;
  line-height: 36px;
}

@media @small-widths {
  .main-block::before {
    left: 20px;
  }
  .main-block-step {
    margin-left: 60px;
  }
  .main-block-step-title {
    font-size: 16px;
  }
  .main-block-step-check {
    display: none;
  }
  .index-num {
    padding: 13px;
    max-height: 42px;
    margin: 10px 10px 10px 0;
  }
  .main-block-step-descr {
    font-size: 16px;
  }
  .main-block-form {
    flex-direction: column;
    margin-left: 0;
  }
}
