.footer {
  background-color: #02272e;
  color: #fff;
}

.footer-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 44px 0;
}

.copyright {
  margin-top: 45px;
  color: #fff;
  font-size: 16px;
  line-height: 24px;
}

.footer-logo {
  max-width: 220px;
}

.footer-right {
  padding: 10px;
  width: 100%;
  max-width: 709px;
}

.footer-right-descr {
  color: #fff;
  font-size: 16px;
}

@media @small-widths {
  .footer-content {
    flex-direction: column-reverse;
  }
  .copyright {
    text-align: center;
  }
  .footer-logo {
    margin: 0 auto;
  }
  .footer-right-descr {
    text-align: center;
  }
}
