.form {
  box-sizing: border-box;
  width: 100%;
  max-width: 380px;
  margin: 20px auto 30px;
  padding-top: 20px;
}
.form-input {
  position: relative;
  display: block;
  margin-bottom: 20px;
}
.form-input-descr {
  position: absolute;
  left: 17px;
  top: 14px;
  font-family: "Graphik", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.3;
  color: #808080;
  transition: font-size 0.3s, color 0.3s, top 0.3s;
  z-index: 2;
}
.form-input.touched .form-input-descr {
  top: -14px;
  font-size: 12px;
  color: #ffffff;
}
.form-input-descr-phone {
  position: absolute;
  left: 17px;
  top: -14px;
  font-family: "Graphik", sans-serif;
  font-size: 12px;
  font-weight: normal;
  line-height: 1.3;
  color: #ffffff;
  transition: font-size 0.3s, color 0.3s, top 0.3s;
}
.form-input-descr-phone.touched {
  top: -14px;
  font-size: 12px;
  color: #ffffff;
}
.form-input input {
  box-sizing: border-box;
  width: 100%;
  padding: 12px 17px;
  background-color: #f0f0f0;
  border: 1px solid #f0f0f0;
  border-radius: 3px;
  font-family: "Graphik", sans-serif;
  font-size: 14px;
  font-weight: normal;
  line-height: 1.3;
  color: #444444;
}
.form-input input.error {
  border: solid 1px #ed145b;
}
.form-input input.valid {
  border: solid 1px #009456;
}
.form-input input:focus {
  outline: none;
}
.form-input input[name="password"] {
  display: block !important;
}
.form-input input::-webkit-input-placeholder {
  color: #939393;
  line-height: 1.3;
  font-weight: normal;
}
.form-input input::-moz-placeholder {
  color: #939393;
  line-height: 1.3;
  font-weight: normal;
}
.form-input input:-moz-placeholder {
  color: #939393;
  line-height: 1.3;
  font-weight: normal;
}
.form-input input:-ms-input-placeholder {
  color: #939393;
  line-height: 1.3;
  font-weight: normal;
}
button.form-btn {
  position: relative;
  width: 100%;
  display: block;
  padding: 14px 10px;
  background-color: #111111;
  text-transform: uppercase;
  border: none;
  border-radius: 3px;
  font-size: 18px;
  font-weight: bold;
  color: #ffffff;
  text-align: center;
  transition: background-color 0.3s;
  cursor: pointer;
}
.form-btn:hover {
  background-color: #284237;
}
.errors-block {
  margin-top: 5px;
  font-size: 12px;
  font-weight: normal;
  color: #cc002f;
  text-align: left;
}
.input-hidden {
  position: absolute;
  width: 0;
  height: 0;
  overflow: hidden;
  visibility: hidden;
  opacity: 0;
}
.form-input-password-wrapper {
  position: relative;
  display: block;
}
.form-input-password-wrapper input {
  padding-right: 44px;
}
.form-input-password-wrapper::before {
  content: "";
  position: absolute;
  left: 5px;
  top: 100%;
  width: 0;
  height: 2px;
  transition: width 0.3s, background-color 0.3s;
}
.form-input-password-wrapper.low::before {
  width: 33%;
  background-color: #c00;
}
.form-input-password-wrapper.medium::before {
  width: 67%;
  background-color: #fb0;
}
.form-input-password-wrapper.high::before {
  width: calc(100% - 10px);
  background-color: #009456;
}
.form-input-phone {
  display: flex;
}
.form-input-phone .intl-tel-input {
  flex-grow: 1;
}
.intl-tel-input.separate-dial-code .selected-dial-code {
  font-size: 14px;
  color: #444444;
}
.form-input .select2 {
  width: 100% !important;
  text-align: left;
}
.form-input .select2-selection.select2-selection--single {
  height: 44px !important;
  background-color: #f0f0f0 !important;
  border: 1px solid #f0f0f0 !important;
  border-radius: 3px !important;
}
.form-input .select2-selection.select2-selection--single.error {
  border: solid 1px #ed4757;
}
.form-input .select2-selection__rendered {
  font-family: "Graphik", sans-serif;
  font-size: 14px;
  line-height: 44px !important;
  color: #444444 !important;
}
.select2-container .select2-selection--single .select2-selection__rendered {
  padding-left: 17px;
}
.form-input .select2-selection__arrow {
  width: 44px !important;
  height: 40px !important;
}
.form-input .select2-selection__placeholder {
  color: #939393 !important;
}
.select-country.error + .select2 .select2-selection {
  border: solid 1px #ed4757;
}
.select-country.valid + .select2 .select2-selection {
  border: solid 1px #009456;
}
.select-country + .select2.select2-container--focus .select2-selection {
  outline: none;
}
.select2-search__field {
  font-family: "Graphik", sans-serif !important;
  font-size: 14px !important;
  color: #333333 !important;
}
.select2-results__option {
  font-family: "Graphik", sans-serif !important;
  font-size: 14px !important;
  color: #333333 !important;
}
.promo-form-descr {
  font-size: 12px;
  color: #ffffff;
}
.modal-dialog .promo-form-descr {
  color: #ffffff;
}
.promo-form-descr a {
  color: #ffffff;
}
.form-descr {
  margin-top: 15px;
  text-align: center;
  font-family: "Graphik", sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3;
  color: #ffffff;
}
.form-descr a {
  color: #ffffff;
  text-decoration: underline;
}
button.form-btn ._preloader {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.55);
  visibility: hidden;
}
button.form-btn.waiting ._preloader {
  visibility: visible;
  z-index: 500;
}
button.form-btn ._loader {
  display: block;
  position: relative;
  left: 50%;
  top: 50%;
  width: 150px;
  height: 150px;
  margin: -75px 0 0 -75px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #45bead;
  -webkit-animation: spin 2s linear infinite;
  animation: spin 2s linear infinite;
}
button.form-btn ._loader:before {
  content: "";
  position: absolute;
  top: 5px;
  left: 5px;
  right: 5px;
  bottom: 5px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #ff1326;
  animation: spin 3s linear infinite;
}
button.form-btn ._loader:after {
  content: "";
  position: absolute;
  top: 15px;
  left: 15px;
  right: 15px;
  bottom: 15px;
  border-radius: 50%;
  border: 3px solid transparent;
  border-top-color: #009456;
  animation: spin 1.5s linear infinite;
}
button.form-btn ._loader-text {
  position: relative;
  transform: translate(-50%, -50%);
  top: 52%;
  left: 50%;
  display: block;
  font-size: 28px;
  color: #ffffff;
  text-transform: none;
}
@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.popup.opened {
  opacity: 1;
  visibility: visible;
  transition: opacity 0.3s, visibility 0.3s;
}

.popup {
  box-sizing: border-box;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  padding: 40px 20px;
  opacity: 0;
  visibility: hidden;
  background-color: rgba(51, 51, 51, 0.39);
  font-size: 0;
  text-align: center;
  white-space: nowrap;
  overflow-x: hidden;
  overflow-y: auto;
  z-index: 1200;
}

.popup::before {
  content: "";
  height: 100%;
  width: 0;
  background-color: #fff;
}

.popup-wrapper,
.popup::before {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
}

.popup.opened .popup-wrapper {
  transform: scale(1);
  transition: transform 0.3s;
}

.popup-wrapper {
  position: relative;
  background-color: #f3f7ff;
  width: 100%;
  max-width: 660px;
  padding: 40px 30px;
  box-shadow: 0 0 20px 0 rgb(0 0 0 / 30%);
  transform: scale(0);
}

.popup-wrapper,
.popup::before {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: middle;
}

.popup-btn-close {
  position: absolute;
  top: 5px;
  right: 5px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  z-index: 1200;
}

.popup-btn-close::before {
  transform: translate(-50%, -50%) rotate(-45deg);
}

.popup-btn-close::after,
.popup-btn-close::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 22px;
  height: 2px;
  background-color: #979797;
  border-radius: 1px;
  transition: background-color 0.3s;
}

.popup-btn-close::after {
  transform: translate(-50%, -50%) rotate(45deg);
}

.popup-btn-close::after,
.popup-btn-close::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  width: 22px;
  height: 2px;
  background-color: #979797;
  border-radius: 1px;
  transition: background-color 0.3s;
}

.popup-content {
  box-sizing: border-box;
  width: 100%;
  white-space: normal;
}

.popup-text {
  box-sizing: border-box;
  width: 100%;
  max-width: 455px;
  margin: 0 auto 50px;
  font-size: 20px;
  font-weight: 400;
  color: #333;
  text-align: center;
}

@media @extraSmall-widths {
  .form {
    margin-left: auto;
    margin-right: auto;
  }
}
