.header-top {
  padding: 38px 10px;
}

.header-logo {
  margin: 0 auto;
  max-width: 300px;
}

.header-bottom {
  background-image: url("../img/main-bg.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.glass {
  background-color: rgba(1, 44, 45, 0.82);
}

.header-text {
  font-family: "Gotham Pro", sans-serif;
  margin: 0 auto;
  padding: 60px 0;
  width: 100%;
  max-width: 1040px;
  text-align: center;
  color: #fff;
}

.header-title {
  font-weight: 700;
  font-size: 44px;
  line-height: 60px;
}

.header-descr {
  color: #fff;
  margin: 40px 0;
  font-size: 24px;
  line-height: 40px;
}

.header-action {
  color: #fff;
  font-weight: 700;
  font-size: 44px;
  line-height: 60px;
  margin-bottom: 0;
}

@media @small-widths {
  .header-title {
    font-size: 33px;
  }
  .header-descr {
    font-size: 20px;
  }
  .header-action {
    font-size: 32px;
  }
}

@media @exxSmall-widths {
  .header-title {
    font-size: 28px;
    line-height: 40px;
  }
  .header-descr {
    font-size: 18px;
    line-height: 28px;
  }
  .header-action {
    font-size: 28px;
    line-height: 40px;
  }
}
