body {
  min-width: 320px;
  font-family: "Roboto", sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

p {
  color: #111111;
  font-size: 18px;
  line-height: 26px;
}

* {
  box-sizing: border-box;
}

button {
  border: none;
}

.colored {
  color: @brand-color;
}
